@import url(https://fonts.googleapis.com/css?family=IBM+Plex+Sans:100,100i,400,400i,700,700i&display=swap);
.Task {
  background-color: azure;
  border-bottom: 1px dashed lightseagreen;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Task .crossed {
  text-decoration: line-through;
  color: #94d4d4;
}
.Task-text {
  color: #173030;
  font-weight: bold;
  cursor: pointer;
  text-align: center;
  width: 82%;
  min-height: 50px;
  -webkit-transition: color 0.5s;
  transition: color 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Task-text:hover {
  color: #94d4d4;
}

.Task-icons {
  width: 18%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.Task-icon {
  font-size: 1.2rem;
  cursor: pointer;
}

.Task-icon:hover {
  color: slategrey;
}

.fa-trash-alt:not(:hover) {
  color: maroon;
}

.fa-edit:not(:hover) {
  color: sandybrown;
}

.fa-save:not(:hover) {
  color: steelblue;
}

.Task input {
  text-align: center;
  height: 50px;
  width: 100%;
  border: none;
  padding: 0;
  background-color: azure;
}

.NewTaskForm input {
  margin: 0;
  height: 50px;
  width: 82%;
  border: none;
  text-align: center;
}

.NewTaskForm button {
  margin: 0;
  height: 50px;
  width: 18%;
  background-color: steelblue;
  border: none;
  text-transform: uppercase;
  color: white;
  font-weight: bold;
}

.NewTaskForm button:hover {
  cursor: pointer;
  background-color: rgb(101, 175, 236);
}

.TaskList {
  margin:auto;
  text-align: center;
  background-color: teal;
  width: 500px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}

@media (max-width: 576px) {
  .TaskList {
  width: 95%
  }
}

.TaskList-title {
  color: white;
  background-color: darkcyan;
  font-size: 2rem;
  margin: 20px 0 0 0;
  padding: 10px;
  border-bottom: 1px solid darkslategray;
}


body {
  font-family: 'IBM Plex Sans', sans-serif;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin: 0;
  padding: 0;
}

#bg {
  height: 100vh;
  width: 100vw;
  position: fixed;
  z-index: -1;
  background-image: url(/modern-react-bootcamp-exercises/s15-todo-list/static/media/bg.4f96b4e8.svg);
  background-size: cover;
  /* background by SVGBackgrounds.com */
}

#root {
  width: 100%;
}

